@import "src/common/styles/sass/variables";
.title {
    margin: 0 0 30px;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size:24px;
    line-height: 1.2em;
    color: #151C25;
    position: relative;
    //letter-spacing: 1px;
    text-transform: capitalize;
.title h2{
    margin: 0;
    color: gold;
}


}
.title::before{
    content: '';
    position: absolute;
    background:  #E71D36;
    width:35px;
    height: 2px;
    bottom: -30px;
    left: 50%;
    transform: translate(-50%,50%);
    //border-radius:70%;

}


