@import "src/common/styles/sass/variables";

.skillsBlock {
    /*min-height: 100vh;*/
    background-color: #334452;

}

.skillsContainer {
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
    background-color: #FFFFFF;
    padding: 100px 0;

}


.skills {

    margin-top: 30px;
    display: flex;
    justify-content: space-around;
    text-align: center;
    flex-wrap: wrap;
    //color: #FFFFFF;
    color: #505050;
    padding: 5px 15px 50px;
}
.faJs,.faCss3,.faReact,.faInfinity{
    color: #E71D36;
    font-size: 50px;
}


@media screen and (max-width: 375px){
.skills{
    justify-content: center;
}
}


