.contactsBlock {
    /*height: 100vh;*/
    background-color: #334452;
}

.contactsContainer {
    flex-direction: column;
    justify-content: flex-start;
    background-color: #FFFFFF;
    padding: 100px 0;
}


.form {
    width: 50%;
    margin-top: 30px;
    display: flex;
    flex-direction: column;

    align-items: center;

}

.form input {
    height: 25px;
    width: 100%;
    /*margin-top: 15px;*/
    background-color: #D6D9DC;
    border: 0;
    color: #334452;
    /*margin: 5px;*/
    margin-bottom: 15px;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;

}

.form textarea {
    height: 50px;
    border: 0;
    /*margin-top: 10px;*/
    background-color: #D6D9DC;
    width: 100%;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    color: #334452;

}

.submitButton {
    margin-top: 20px;
    padding: 10px 30px;
    color: white;
    background-color: #E71D36;
    border: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 13px;
    cursor: pointer;
}

.submitButton:hover{
    opacity: 80%;
}

@media screen and (max-width: 375px) {
    .form {
        border: 1px solid;
        width: 100%;
        /*height: 250px;*/
        display: flex;
        flex-direction: column;
        /*flex-wrap: wrap;*/

        justify-content: space-around;
    }

    .form input {
        height: 25px;

        margin-top: 15px;
    }

    .form textarea {
        height: 50px;

        margin-top: 10px;
        /*flex: 1 1 auto;*/
    }

    .contactsContainer button {

        border: none;
        color: #5f715f;
        display: flex;
        flex-wrap: wrap-reverse;
        align-items: flex-end;
        align-content: center;
        flex-direction: column-reverse;
        margin-top: 80px;
        width: 160px;
        height: 40px;
        align-items: initial;
        justify-content: center;


    }
}






