@import "src/common/styles/sass/variables";
.skill{
    width: 250px;
    flex-direction: column;
    display: flex;
    margin-top: 20px;
    padding-left: 5px;
    padding-right: 5px;
}
.titleForSkill{
    color: #334452;
}

.description{
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
    color: #788186;
    line-height: 20px;

}

@media screen and (max-width: 375px){
    .skill{
        justify-content: center;
    }
}