.mainBlock{
    min-height: 100vh;
    background-color: #334452;


}

.container{
    flex-wrap: wrap;
    background-color: white;
    min-height: 100vh;
    padding: 100px 0 0;
}
.particles{
    position: absolute;
    top:0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.3;
}

.greeting{
    width: 300px;
    background-color: white;
    font-family: 'Roboto', sans-serif;
    font-size: 22px;
}

.greeting h1{
    position:relative ;
}

.greeting .hi{
    color: #E71D36;

    font-size: 17px;
    font-weight: 500;
}
.greeting p,.greeting span{
    color: #808080;
    font-weight: 500;
    font-size: 17px;
    margin-top: 55px;
}


.photo{
    width: 400px;
    height: 500px;
    background-color: azure;
    /*height: 300px;*/
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;



}


.greeting h1::before{
    content: '';
    position: absolute;
    background:  #E71D36;
    width:35px;
    height: 2px;
    bottom: -30px;
    /*left: 50%;*/
    /*transform: translate(-150%,30%);*/
//border-radius:70%;

}
