.nav {
    width: 320px;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    flex-wrap: nowrap;
    color: #FFFFFF;

}

a{
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
    text-decoration: none;
    cursor: pointer;
}
.active{
    color: #505050
}
a:hover{
    color: #66737D
}

@media screen and (max-width: 400px){
    .nav{
    display:none;


}
}