.burgerNav {
    width:100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: stretch;
    flex-wrap: nowrap;
    color: #FFFFFF;

    //border: 1px solid red;
    //position: relative;
}

.burgerNavItems{
    //display: flex;
    justify-content: space-between;
    text-align: center;
    flex-wrap: nowrap;
    flex-direction: column;
    position: absolute;
    right: 40px;
    display: none;
    transform: translate(0,-150px);
    //transition: 0.7s;



    /////добавил и все потекло
    //flex-direction: column;
    //text-align: center;
    //flex-wrap: nowrap;

}
.show{
    display: flex;
    flex-direction: column;
    text-align: center;
    flex-wrap: nowrap;
    transform: translate(0,0);
    transition: 0.7s;
    height: 200px;
    padding: 20px;

}
a{
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
    text-decoration: none;
    cursor: pointer;
    color: #FFFFFF;
}
.active{
    color: #505050
}
a:hover{
    color: #66737D
}
.burgerBtn{
    width: 30px;
    height: 30px;
    background-color:#E71D36 ;
    position: absolute;


    top: 50%;
    right: 20px;
    transform: translate(0, -50%);
}
@media screen and (min-width: 400px){
   .burgerNav{
       display:none;
       //color: red;
   }

}


