@import "./src/common/styles/sass/variables.scss";

.header {
    min-height: 80px;
    background-color:#06233B;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: fixed;
    z-index: 9999;
    left: 0;
    right: 0;
    flex-direction: row;


}

@media screen and (min-width: 400px){
    .header{
        padding-right: 30px;
    }

}

