@import "src/common/styles/sass/variables";

.work {
  width: 450px;
  background-color: #FFFFFF;
  flex-direction: column;
  display: flex;
  color: $textColor;
  margin: 25px;
  box-shadow: 0 0 16px 3px #cccccc;
  .image {
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;

    .image::before {
      position: absolute;
      content: '';
      background-color: $bgColor;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 30%;
    }

    .viewBtn {
      background-color: #334452;
      padding: 10px 20px;
      position: relative;
      z-index: 1;
      border: 0;
      font-family: 'Roboto', sans-serif;
      font-weight: 500;
      font-size: 13px;
      color: #FFFFFF;
      cursor: pointer;
      //.viewBtn:hover{
      //z-index: 1;
      //position: relative;
      //border: 1px solid #334452 ;
      //background-color:#334452 ;
      //}
    }
  }
  .projectInfo {
    padding: 20px;
    .projectTitle {
      color: $textColor;
      font-family: "Roboto", Sans-serif;
      font-size: 19px;
      font-weight: 700;
      line-height: 24px;
    }
    .description {
      color: #828282;
      font-family: "Roboto", Sans-serif;
      font-size: 15px;
      font-weight: 400;
      line-height: 24px;
    }
  }

}

.viewBtn:hover{
//background-color: firebrick;
  opacity: 80%;
  z-index: 1;
  position: relative;
  border: 1px solid #334452 ;
  background-color:#334452 ;

}
